import { getContractInfo } from '../lib/contract'
import { DEFAULT_NETWORK } from './constants'

export default async (tokenId) => {
  const { address } = await getContractInfo(DEFAULT_NETWORK)
  return DEFAULT_NETWORK === 4 ? 
    `https://testnets.opensea.io/assets/rinkeby/${address}/${tokenId}`
  :
    `https://opensea.com/assets/ethereum/${address}/${tokenId}/`
}