<script>
  import { onMount } from 'svelte'
  import { signerAddress } from 'svelte-ethers-store'

  import { mySlashesIds, mySlashes } from '../store'

  import Thumbnail from '../components/Shape/Thumbnail.svelte'
  import { getSlashesPNG, getSlashesSVG } from '../lib/api'

  export let slashesContract

  let balance = 0
  // let tokens = []
  onMount(async function () {
    balance = await slashesContract.balanceOf($signerAddress)
    const tokenIds = await Promise.all(
      Array(balance.toNumber())
        .fill(0)
        .map((_, i) => slashesContract.tokenOfOwnerByIndex($signerAddress, i))
    )
    mySlashesIds.set(tokenIds.map(d => d.toNumber()))
    // tokens = get(slashes).filter(({ tokenId }) => tokenIds.map((d) => d.toNumber()).includes(tokenId))
  })
</script>

<div class="h-0.5 mx-auto mt-16 md:mt-24 max-w-full mb-12 w-96 bg-gradient-to-r from-transparent via-white/20" />

<h3 class="text-lg font-semibold text-gray-50">My Slashes ({$mySlashesIds.length})</h3>

<p class="max-w-xl mt-2 text-sm leading-relaxed text-gray-400">
  These are the $SLASHES tokens you owned from the previous contract.
</p>

<section class="grid grid-cols-2 gap-4 mt-8 sm:gap-6 md:gap-8 sm:grid-cols-3 md:grid-cols-4 xl:grid-cols-6 p-3 -mx-3">
  {#each $mySlashes as { tokenId }}
    <div>
      <Thumbnail
        {tokenId}
        png={getSlashesPNG(tokenId)}
        upgradable={true}
      />
    </div>
  {/each}
</section>
