<script>
  import { onMount } from 'svelte'

  // components
  import Header from './components/Header.svelte'
  import Footer from './components/Footer.svelte'
  import ConnectWallet from './components/ConnectWallet.svelte'
  import InfoBubbles from './components/InfoBubbles.svelte'
  import Message from './components/Message.svelte'
  import Prompt from './components/Prompt.svelte'

  // import Countdown from './components/Countdown.svelte'
  import SingleShape from './layouts/SingleShape.svelte'
  import SingleSlash from './layouts/SingleSlash.svelte'

  // advanced components
  import MySlashes from './layouts/MySlashes.svelte'
  import MyShapes from './layouts/MyShapes.svelte'
  import AllShapes from './layouts/AllShapes.svelte'

  import { contracts } from 'svelte-ethers-store'
  import { shownTokenId } from './store'
  
  import { CURRENT_UI_SHAPE } from './lib/constants';
  
  // contracts
  let shapesContract
  let slashesContract
  contracts.subscribe(({ shapes, slashes }) => {
    shapesContract = shapes
    slashesContract = slashes
  })

  // set shown token id
  let isSlashes
  let isCongrats
  function handleHashChange() {
    const hash = window.location.hash
    isSlashes = hash.includes('slashes')
    isCongrats = hash.includes('congrats')
    const tokenId = parseInt(
      hash.slice(
        // remove prefix if specified
        isSlashes ? 1 + 'slashes-'.length
        :
        isCongrats? 1 + 'congrats-'.length
        :
        1
      )
    )
    console.log(tokenId || 'no token id', isSlashes)
    if(!isNaN(tokenId)) {
      shownTokenId.set([tokenId, isSlashes])
    }
  }

  // fired when url hash changes
  let isLoaded = false
  onMount(() => {
    handleHashChange()
    setTimeout(() => {
      isLoaded = true
    }, 0)

  })

  // pick the correct token type
  export const defaultShapeType = CURRENT_UI_SHAPE
</script>

<svelte:window on:hashchange={handleHashChange} />

<div class="px-4 bg-gray-900 pt-32 xl:pt-40 sm:px-6 md:px-8">
  <div class="transition-opacity duration-1000 delay-500 {isLoaded ? '' : 'opacity-0'}">
    <div class="fixed w-full top-0 right-0 p-2 md:translate-x-0 md:p-8 z-30 text-right pointer-events-none">
      <div class="flex flex-col gap-2 md:gap-4 items-center md:items-end">
        <ConnectWallet />
        <Message />
      </div>
    </div>

    <div class="max-w-4xl mx-auto xl:max-w-7xl text-gray-50">
      <img src="./images/bg.png" alt="Background" class="absolute top-0 w-auto -translate-x-1/2 left-1/2" />

      <div class="relative">
        <!-- Show a single token -->
        {#if $shownTokenId[0] !== 0}
          {#if $shownTokenId[1] === true}
            <SingleSlash tokenId={$shownTokenId[0]}/>
          {:else}
            <SingleShape tokenId={$shownTokenId[0]} congrats={isCongrats}/>
          {/if}
        {/if}

        <Header shapeType={defaultShapeType} />

        <!-- <section class="text-center">
          <Countdown />
        </section> -->

        <InfoBubbles />

        <Prompt {isLoaded}/>

        {#if slashesContract && shapesContract}
          <MySlashes {slashesContract} />
        {/if}

        {#if shapesContract}
          <MyShapes {shapesContract} />
        {/if}

        <!-- Show all shapes from API -->
        <AllShapes />

      </div>
    </div>
  </div>
</div>
<Footer />
