<script>
  import { get } from 'svelte/store';
  import { shapes } from '../store'
  import { fetchToken } from '../lib/api'
  import { PROD_API_BASE_URL, DEV_API_BASE_URL } from '../lib/constants'

  import Modal from '../components/Shape/Modal.svelte'

  // props
  export let tokenId = 0
  export let congrats = false
  
  const changeAllowed = false

  // no default
  export let png = ''
  export let svg = ''

  // parse dev server URL
  if (process.env.USE_LOCALHOST) {
    png = png.replace(PROD_API_BASE_URL, DEV_API_BASE_URL)
    svg = svg.replace(PROD_API_BASE_URL, DEV_API_BASE_URL)
  }

  function getSingleToken() {
    return new Promise((resolve, reject) => {
      const existingShapeIndex = get(shapes)
        .map(({tokenId: id }) => id)
        .indexOf(tokenId)
      if(existingShapeIndex !== -1 ) {
        resolve(get(shapes)[existingShapeIndex])
      } else {
        fetchToken(tokenId)
          .then(token => resolve(token))
          .catch(err => reject(err))
      }
    })
  }

</script>

{#await getSingleToken()}
  loading...
{:then {png, svg, attributes}}
  {#if png}
  <Modal
    {tokenId}
    {png}
    {svg}
    {attributes}
    {changeAllowed}
    {congrats}
  /> 
  {:else}
    No token with id {tokenId} found.
  {/if}
{:catch}
  An error occured while fetching the token...
{/await}


