// load all deployments
const deployments = import.meta.glob('../../smart-contracts/deployments/**/*.json')

const chains = {
    rinkeby: 4,
    ropsten: 3,
    mainnet: 1,
}

export const getNetworkName = (chainId) => {
    if (chainId === 31337) { // for hardhat
        return 'localhost'
    }

    const networkName = Object.keys(chains).find((k) => chains[k] === chainId)
    if (!networkName) throw new Error(`Network ${chainId} not supported.`)
    return networkName
}

export const getDeployment = async (chainId, contractName) => {

    if (!contractName) throw new Error(`Missing contract name.`)
    if (!chainId) throw new Error(`Missing chain Id.`)

    const networkName = getNetworkName(chainId)
    const deploymentPath = `../../smart-contracts/deployments/${networkName}/${contractName}.json`
    
    return await deployments[deploymentPath]()
}