<script>
  import { clickOutside } from '../clickOutside.js'
  import Attributes from './Attributes.svelte'
  import ShareButtons from './ShareButtons.svelte'
  import ChangeShape from './ChangeShape.svelte'
  import ChangeSeed from './ChangeSeed.svelte'
  import SwapSlash from './SwapSlash.svelte'
  import { shownTokenId, isDoingSthImportant } from '../../store'
  import { getTitle } from '../../lib/helpers'

  export let tokenId
  export let png
  export let svg
  export let congrats = false
  export let upgradable = false
  export let changeAllowed = false
  export let attributes = []

  const [title, shapeName] = getTitle(attributes)

  function handleKeydown(e) {
    if (e.key === 'Escape') close()
  }

  function close() {
    if ($isDoingSthImportant) return
    shownTokenId.set([0, false])
    history.pushState("", document.title, window.location.pathname); 
  }

  function handleImageLoadError() {
    // TODO: handle missing image
    console.log('Image errored')
  }
</script>


<svelte:window on:keydown={handleKeydown} />

<div 
  class="inset-0 bg-black/80 z-20 fixed transition-colors duration-500 backdrop-blur-lg" 
  on:click={close} 
  />

<div class="relative z-40 group h-fit" use:clickOutside on:click-outside={close}>
  <div class="absolute w-full h-full placeholder" />
  <div
    class="bg-gray-900 border rounded-lg border-white/10 text-gray-50 max-w-[calc(100vw-32px)] no-scrollbar transition-none h-full overscroll-contain overflow-auto lg:overflow-hidden fixed !top-1/2 !left-1/2 !w-[calc(60vh+400px)] !h-[90vh] -translate-x-1/2 -translate-y-1/2 p-6 rounded-2xl"
  >
    <div class="relative z-20 bg-gray-800 aspect-2/3 lg:max-h-full mx-auto lg:mx-0 max-h-[80%] rounded-lg">
      <img
        src={png}
        class="object-cover w-full h-full rounded-lg"
        alt="Slashes #{tokenId}"
        on:error={handleImageLoadError}
      />
    </div>
    <div class="lg:absolute lg:right-0 lg:top-0 lg:w-[400px] lg:h-[calc(100%-0.5rem)] mt-1">
      <div>
        <div
          class="hidden lg:block absolute left-0 z-10 w-full h-32 -mt-px pointer-events-none -top-1 bg-gradient-to-b from-gray-900"
        />
        <div
          class="hidden lg:block absolute left-0 z-10 w-full h-32 -mt-px pointer-events-none -top-1 bg-gradient-to-b from-gray-900"
        />
        <div
          class="hidden lg:block absolute left-0 z-10 w-full h-32 -mt-px pointer-events-none -bottom-1 bg-gradient-to-t from-gray-900"
        />
        <div
          class="hidden lg:block absolute left-0 z-10 w-full h-32 -mt-px pointer-events-none -bottom-1 bg-gradient-to-t from-gray-900"
        />
        <div
          class="absolute z-30 items-center gap-2 p-2 transition rounded-lg cursor-pointer right-4 top-2 text-white/40 hover:bg-white/5 hidden lg:flex"
          on:click|stopPropagation={close}
        >
          Close
          <kbd class="px-1 py-0.5 font-sans text-xs rounded bg-white/10">esc</kbd>
        </div>
        <div
        class="lg:absolute lg:top-0 z-0 w-full h-full lg:py-32 pb-12 overflow-auto overscroll-contain transition-all duration-500 lg:-translate-x-1/2 lg:left-1/2 no-scrollbar delay-200"
        >
        {#if congrats} 
        <div class=" p-8 rounded-lg -mx-8 relative overflow-hidden">
          <h2 class="text-[#F6B855] text-center text-xl">
            Here is your new shape. 
            <br /> 
            Congrats!
          </h2>
        </div>
        {/if}

          <div class="w-64 mx-auto max-w-full">
            <div class="space-y-2">
              <h1 class="text-2xl lg:text-3xl text-center">{title} #{tokenId}</h1>
              <h2 class="flex justify-center items-center gap-2 text-gray-400">
                <svg
                  class="w-5 h-5 stroke-current"
                  viewBox="0 0 16 16"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                  stroke-width="1.25"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                >
                  <path
                    d="M4.33333 6.66667C5.622 6.66667 6.66667 5.622 6.66667 4.33333C6.66667 3.04467 5.622 2 4.33333 2C3.04467 2 2 3.04467 2 4.33333C2 5.622 3.04467 6.66667 4.33333 6.66667Z"
                  />
                  <path d="M1.6665 14.0002H6.99984L4.33317 9.3335L1.6665 14.0002Z" />
                  <path d="M9.3335 2L14.0002 6.66667" />
                  <path d="M9.3335 6.66667L14.0002 2" />
                  <path d="M9.3335 9.3335H14.0002V14.0002H9.3335V9.3335Z" />
                </svg>
                <span class="text-lg lg:text-xl first-letter:capitalize">{shapeName}</span>
              </h2>
            </div>

            <!-- Swap change etc -->
            {#if changeAllowed}
              <div class="h-px max-w-full mx-auto my-12 w-96 bg-gradient-to-r from-transparent via-white/20" />
              <ChangeSeed {tokenId} />
              <ChangeShape {tokenId} />
            {/if}

            {#if upgradable}
              <div class="h-px max-w-full mx-auto my-12 w-96 bg-gradient-to-r from-transparent via-white/20" />
              <SwapSlash {tokenId} />
            {/if}

            <!-- Twitter etc -->
            <div class="h-px max-w-full mx-auto my-12 w-96 bg-gradient-to-r from-transparent via-white/20" />
            <ShareButtons {tokenId} {png} {svg} />

            {#if attributes.length}
              <div class="h-px max-w-full mx-auto my-12 w-96 bg-gradient-to-r from-transparent via-white/20" />
              <Attributes {attributes} />
            {/if}
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
