<script>
  import { onMount } from 'svelte'
  import { signerAddress } from 'svelte-ethers-store'
  import { myShapes } from '../store'
  import { fetchShapesByIds } from '../lib/api'
  import Thumbnail from '../components/Shape/Thumbnail.svelte'

  export let shapesContract

  let balance = 0
  // let tokens = []
  onMount(async function () {
    const balanceOf = await shapesContract.balanceOf($signerAddress)
    balance = balanceOf.toNumber()
    if (balance) {
      const tokenIds = await Promise.all(
        Array(balance)
          .fill(0)
          .map((_, i) => shapesContract.tokenOfOwnerByIndex($signerAddress, i))
      )
      const tokens = await fetchShapesByIds(tokenIds.map(d => d.toNumber()))
      myShapes.set(tokens)
    }
  })
</script>

<div class="h-0.5 mx-auto mt-16 md:mt-24 max-w-full mb-12 w-96 bg-gradient-to-r from-transparent via-white/20" />

<h3 class="text-lg font-semibold text-gray-50">My Shapes ({$myShapes.length})</h3>

<p class="max-w-xl mt-2 text-sm leading-relaxed text-gray-400">These are the $SHAPES tokens you own.</p>

<section class="grid grid-cols-2 gap-4 mt-8 sm:gap-6 md:gap-8 sm:grid-cols-3 md:grid-cols-4 xl:grid-cols-6 p-3 -mx-3">
  {#each $myShapes as { tokenId, png, svg, attributes, loading }}
    <div>
      <Thumbnail {tokenId} {png} {attributes} />
    </div>
  {/each}
</section>
