<script>
  import { DEFAULT_SHAPE } from '../../lib/constants'
  import { connected, signer, contracts } from 'svelte-ethers-store'
  import { getTxUrl } from '../../lib/contract'
  import { fetchToken } from '../../lib/api'
  import { shapes, myShapes, mySlashesIds, isDoingSthImportant } from '../../store'
  import { get } from 'svelte/store'

  export let tokenId

  let isProcessing = false
  let approvalTxHash
  let approvalDone = false
  let swapTxHash
  let swapDone = false

  async function handleUpgradableClick() {
    if (!connected) {
      throw Error('No account connected.')
    }

    isProcessing = true
    isDoingSthImportant.set(true)

    // approve then swap the slash
    const approvalTx = await $contracts.slashes.connect($signer).approve($contracts.shapes.address, tokenId)
    approvalTxHash = getTxUrl(approvalTx.hash)

    const { transactionHash: swapTransactionHash } = await approvalTx.wait()
    approvalTxHash = getTxUrl(swapTransactionHash)

    approvalDone = true

    // then we can actually swap the token
    const tx = await $contracts.shapes.swapSlash(tokenId, DEFAULT_SHAPE)
    swapTxHash = getTxUrl(tx.hash)

    // wait until the transaction is mined
    const { events, transactionHash } = await tx.wait()
    swapTxHash = getTxUrl(transactionHash)

    // get Transfer event from the tx data
    // const { args } = events.find(({ event }) => event === 'SwapSlashes')
    const {
      args: { tokenId: newTokenId },
    } = events.find(({ event }) => event === 'Transfer')

    // notify the server to rebuild this
    const token = await fetchToken(newTokenId)

    // update the shapes
    shapes.set([...get(shapes), token])
    myShapes.set([...get(myShapes), token])

    // remove the slash from ui
    mySlashesIds.set(get(mySlashesIds).filter((d) => d !== tokenId))
    swapDone = true
    
    // wait for 3s to fetch image then go to the new shape
    setTimeout(() => {
      isDoingSthImportant.set(false)
      window.location.hash = `congrats-${newTokenId}`
    }, 3000)
  }
</script>

<div class="bg-gray-800 p-8 rounded-lg -mx-8 relative overflow-hidden">
  <div class="absolute inset-0 w-full h-px bg-gradient-to-r from-transparent via-white/20" />

  <img
    src="./images/bg.png"
    alt="Background"
    class="absolute object-cover left-0 top-0 rotate-90 -translate-y-4 opacity-10"
  />

  <div class="relative">
    <h3 class="text-center text-xl text-[#F6B855]">Upgrade this token</h3>
    <p class="text-sm mt-4 leading-relaxed">
      You can swap this token to a new $SHAPES token. This token will be burned and you will receive a new token in
      exchange.
    </p>

    <p class="mt-6 text-[#F6B855] border border-[#F6B855]/50 p-4 rounded-lg text-sm bg-[#F6B855]/10 leading-relaxed">
      This new token will have new features: the art will not be the same. The Slashes V1 token that you currently own
      will be burned.
    </p>

    {#if isProcessing}
      <div class="mt-10 text-white text-sm leading-relaxed" role="status">
        {#if !approvalDone && !approvalTxHash}
          <p class="text-[#F6B855] text-base mb-2 block w-full text-center">Step 1/4</p>

          <p>Please approve the SLASHES contract to burn your token.</p>
        {/if}

        {#if !approvalDone && approvalTxHash}
          <p class="text-[#F6B855] text-base mb-2 block w-full text-center">Step 2/4</p>

          <p>
            Approving the contractâ€¦ This might take 10-20 seconds (<a
              class="underline decoration-dashed underline-offset-4 hover:decoration-solid"
              href={approvalTxHash}
              target="_blank">see transaction</a
            >).
          </p>
        {/if}

        {#if approvalDone && !swapDone && !swapTxHash}
          <p class="text-[#F6B855] text-base mb-2 block w-full text-center">Step 3/4</p>

          <p>Please confirm that you want to swap the token.</p>
        {/if}

        {#if approvalDone && !swapDone && swapTxHash}
          <p class="text-[#F6B855] text-base mb-2 block w-full text-center">Step 4/4</p>

          <p>
            Swapping the tokenâ€¦ This might take 10-20 seconds (<a
              class="underline decoration-dashed underline-offset-4 hover:decoration-solid"
              href={swapTxHash}
              target="_blank">see transaction</a
            >).
          </p>
        {/if}

        {#if approvalDone && swapDone}
          <p class="text-[#F6B855] text-center text-lg mb-2">Upgrade done. Congrats!</p>

          <p>Another 20 seconds to wait while we are fetching your new token...</p>
          <p>Please refresh this page if you have issues viewing it.</p>
        {/if}
      </div>
    {:else}
      <p class="text-[#F6B855] mt-6 text-sm leading-relaxed">
        Upgrading is a four-step process. It will trigger two signed operations in your wallet.
      </p>

      <button
        on:click={handleUpgradableClick}
        class="mt-8 mx-auto py-3 w-full gap-2 bg-[#F6B855] text-black rounded-lg flex items-center justify-center hover:brightness-110"
      >
        <svg
          class="flex-none w-5 h-5 stroke-current"
          viewBox="0 0 16 16"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          stroke-width="1.25"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <path
            d="M6.00006 8.00023H3.6094C3.47756 8.0002 3.3487 7.96108 3.23909 7.88783C3.12948 7.81457 3.04406 7.71046 2.99361 7.58866C2.94316 7.46686 2.92996 7.33284 2.95567 7.20354C2.98139 7.07424 3.04486 6.95546 3.13806 6.86223L7.52873 2.47156C7.65375 2.34658 7.82329 2.27637 8.00006 2.27637C8.17684 2.27637 8.34638 2.34658 8.4714 2.47156L12.8621 6.86223C12.9553 6.95546 13.0187 7.07424 13.0445 7.20354C13.0702 7.33284 13.057 7.46686 13.0065 7.58866C12.9561 7.71046 12.8706 7.81457 12.761 7.88783C12.6514 7.96108 12.5226 8.0002 12.3907 8.00023H10.0001V12.0002H6.00006V8.00023Z"
          />
          <path d="M6 14H10" />
        </svg>
        Okay, upgrade token
      </button>
    {/if}
  </div>
</div>
