export default (tokenId) => {
  const baseURL = `https://makio135.com/slashes-v2`
  const text = `Shapes ${tokenId} by @makio135 and @clemsos!`
  const hashtags = 'shapes,slashes,generative,svg,onchain,nft,solidity'
  return `https://twitter.com/intent/tweet?text=${encodeURI(text)}%0a&hashtags=${encodeURI(hashtags)}&url=${encodeURI(
    baseURL
  )}${tokenId ? '%23' + tokenId : ''}%0a`
}
