<script>
  import { get } from 'svelte/store';
  import { slashes, mySlashesIds } from '../store'
  import { getSlashesPNG, getSlashesSVG } from '../lib/api'

  import Modal from '../components/Shape/Modal.svelte'

  // props
  export let tokenId = 0
  let upgradable = true
  const changeAllowed = false
  
  function getSingleToken() {
    return new Promise((resolve) => {
      // check if upgradable
      upgradable = get(mySlashesIds).includes(tokenId)

      // get token
      const existingSlashIndex = get(slashes).map(({tokenId}) => tokenId).indexOf(tokenId)
      const token = get(slashes)[existingSlashIndex]

      // images 
      const png = getSlashesPNG(tokenId)
      const svg = getSlashesSVG(tokenId)

      resolve({...token, png, svg})
    })
  }
</script>

{#await getSingleToken()}
  loading...
{:then {png, svg, attributes}}
  {#if png}
  <Modal
    {tokenId}
    {png}
    {svg}
    {upgradable}
    {attributes}
    {changeAllowed}
  /> 
  {:else}
    No token with id {tokenId} found.
  {/if}
{:catch}
  An error occured while fetching the token...
{/await}


