<script>
  import { onMount } from 'svelte'
  import { SHAPES_PER_PAGE } from '../lib/constants'
  import Search from '../components/Search.svelte'
  import Thumbnail from '../components/Shape/Thumbnail.svelte'
  import { fetchShapes, fetchCount } from '../lib/api'
  import { shapes, filteredShapes } from '../store'

  let backdrop = false

  // default values
  let loading = false
  let offset = 0
  let limit = SHAPES_PER_PAGE

  async function handleClickLoadMore() {
    offset = $shapes.length
    limit = offset + SHAPES_PER_PAGE
    // console.log({ offset, limit })
    const newShapes = await fetchShapes(limit, offset)
    shapes.set([...$shapes, ...newShapes])
  }

  let totalSupply
  onMount(async () => {
    totalSupply = await fetchCount()
  })
</script>

<Search count={totalSupply} />

<div
  class="z-50 fixed top-8 sm:top-16 right-8 transition-opacity lg:hidden w-12 h-12 flex items-center justify-center bg-gray-800 text-gray-200 hover:text-white border-gray-700 rounded-lg shadow border duration-300 hover:bg-gray-700 cursor-pointer {backdrop
    ? 'delay-500'
    : 'opacity-0 pointer-events-none'}"
>
  <svg
    xmlns="http://www.w3.org/2000/svg"
    viewBox="0 0 24 24"
    stroke-width="1.5"
    stroke-linecap="round"
    stroke-linejoin="round"
    class="w-6 h-6 fill-transparent stroke-current"
  >
    <line x1="18" y1="6" x2="6" y2="18" />
    <line x1="6" y1="6" x2="18" y2="18" />
  </svg>
</div>

<section
  class="grid grid-cols-2 gap-4 mt-8 sm:gap-6 md:gap-8 sm:grid-cols-3 md:grid-cols-4 xl:grid-cols-6 p-3 -mx-3 {backdrop
    ? ''
    : 'overflow-hidden'}"
>
  {#each $filteredShapes as { tokenId, png, attributes }}
    <Thumbnail {tokenId} {png} {attributes}/>
  {/each}
</section>

{#if $filteredShapes.length >= limit}
  <div class="flex justify-center">
    <button
      type="button"
      class="relative py-4 px-12 mt-24 text-sm font-medium text-gray-200 hover:text-white hover:text-gray rounded-lg border border-white/10 bg-white/10 hover:bg-white/15 inline-flex items-center transition-colors duration-150"
      on:click={handleClickLoadMore}
    >
      <div class="absolute inset-0 -top-px w-full h-px bg-gradient-to-r from-transparent via-white/20" />
      {#if loading}
        <svg
          role="status"
          viewBox="0 0 100 101"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          class="inline w-4 h-4 mr-2 text-gray-200 animate-spin dark:text-gray-600"
        >
          <path
            d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
            fill="currentColor"
          />
          <path
            d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
            fill="#1C64F2"
          />
        </svg>
      {/if}
      Load more
    </button>
  </div>
{/if}
