import { writable, derived } from 'svelte/store'
import { fetchSlashes, fetchShapes, updateAllTokens } from './lib/api'

// get all slashes from API
export const slashes = writable([])
fetchSlashes().then((slashesData) => {
  slashes.set(slashesData.map((data, i) => ({ ...data, tokenId: i + 1 })))
})

// fetch slashes
export const shapes = writable([])
fetchShapes().then((shapesData) => {
  const missing = shapesData.filter(({ missing }) => missing).length !== 0
  // send as non blocking
  if(missing) updateAllTokens()
  // set actual data 
  shapes.set(shapesData.map((data) => ({ ...data })))
})

// error message or info
export const message = writable('')

// filter shapes
export const searchTerm = writable('')
export const filterShapeType = writable('')

export const filteredShapes = derived(
  [shapes, searchTerm, filterShapeType],
  ([$shapes, $searchTerm, $filterType]) => {
    return $shapes.length ? 
      $shapes.filter(({ tokenId, shape }) => 
        `${tokenId}`.includes($searchTerm)
        &&
        (shape === $filterType || $filterType === 'all' || $filterType === '')
      )
      .sort((a, b) => a.tokenId - b.tokenId)
      :
      []
  }
)

// user data
export const myShapes = writable([])

export const mySlashesIds = writable([])
export const mySlashes = derived(
  [slashes, mySlashesIds],
  ([$slashes, $mySlashesIds]) => 
    $slashes.filter(({ tokenId }) => 
      $mySlashesIds.includes(tokenId)
    )
  )

// show a single token 
// [number tokenId, bool isSlashes]
export const shownTokenId = writable([0, false])


export const isDoingSthImportant = writable(false)