import { API_BASE_URL, SLASHES_IMAGE_BASE_URL } from './constants'
import { SHAPES_PER_PAGE, CURRENT_UI_SHAPE } from '../lib/constants';
console.log(`Server at: ${API_BASE_URL}`)

export const fetchShapesByIds = async (ids = []) => {
  const res = await fetch(`${API_BASE_URL}?ids=${ids.join()}`)
  const { tokens } = await res.json()
  return tokens
}

export const fetchShapes = async (
  limit = SHAPES_PER_PAGE, 
  offset = 0, 
  shape = CURRENT_UI_SHAPE
) => {
  const res = await fetch(`${API_BASE_URL}?limit=${limit}&offset=${offset}&shape=${shape}`)
  const { tokens } = await res.json()
  return tokens
}

export const fetchCount = async (shape = CURRENT_UI_SHAPE) => {
  const res = await fetch(`${API_BASE_URL}count?shape=${shape}`)
  const { count } = await res.json()
  return count
}

// this will fetch the token from the API 
// API will fetch from contract if the token can not be found in db
// pass force=true to force recreate the thumb
export const fetchToken = async (tokenId, force = false) => {
  const url = `${API_BASE_URL}shapes/${tokenId}${force ? '?force=true' : ''}`
  const res = await fetch(url)
  const token = await res.json()
  return token
}

export const updateAllTokens = async () => {
  const res = await fetch(`${API_BASE_URL}historical`, { method : 'POST' })
  console.log(await res.text())
}

export const getShapesPNG = ({ tokenId, seeds }) => `${API_BASE_URL}/png/${tokenId}-${seeds[seeds.length-1]}.png`

// slashes stuff
export const getSlashesPNG = (tokenId) => `${SLASHES_IMAGE_BASE_URL}/png/${tokenId}.png`
export const getSlashesSVG = (tokenId) => `${SLASHES_IMAGE_BASE_URL}/svg/${tokenId}.svg`

export const fetchSlashes = async () => {
  const resp = await fetch(`${API_BASE_URL}slashes`)
  const { slashes } = await resp.json()
  return slashes
}
