import { contracts } from 'svelte-ethers-store'
import { getDeployment, getNetworkName } from '../../utils/deployments'
import { message } from '../store'
import { BLOCK_EXPLORERS, DEFAULT_NETWORK } from './constants'


export const getContractInfo = async (chainId, contractName = 'Shapes') => {
  // make sure we have deployed contract address
  let manifest
  try {
    manifest = await getDeployment(chainId, contractName)
  } catch (error) {
    if (error.message.includes('not supported')) {
      message.set(`Network ${chainId} not supported.`)
    } else {
      message.set(`Could not find contract info for ${getNetworkName(chainId)}`)
    }
  }
  return manifest
}

export const fetchTokenURI = async (tokenId) => {
  console.log('Fetching tokenURI from chain...')
  const tokenURI = await contracts.shapes.tokenURI(tokenId)
  const json = JSON.parse(
    window.atob(
      tokenURI.replace('data:application/json;base64,', '')
      , "base64"
    )
  )
  return {
    ...json, tokenId
  }
}

export const getTxUrl = (txHash, network = DEFAULT_NETWORK) => `${BLOCK_EXPLORERS[network]}/tx/${txHash}`
export const getAddressUrl = (address, network = DEFAULT_NETWORK) => `${BLOCK_EXPLORERS[network]}/address/${address}`