import { Buffer } from 'buffer'
import { ethers } from 'ethers'
import WalletConnectProvider from '@walletconnect/web3-provider'
import Web3Modal from 'web3modal'
import { defaultEvmStores } from 'svelte-ethers-store'

window.Buffer = Buffer

import { getContractInfo } from './contract'
import rpc from '../../utils/networks'
import { message } from '../store'

const providerOptions = {
  // cacheProvider: false, // prevent from caching
  walletconnect: {
    package: WalletConnectProvider, // required
    options: {
      rpc, // required
    },
  },
}
const web3Modal = new Web3Modal({
  network: 'rinkeby', // optional
  cacheProvider: true, // optional
  theme: 'dark',
  providerOptions, // required
})

export const connectWallet = async () => {
  try {
    console.log('Connecting to wallet...')
    const instance = await web3Modal.connect()
    const provider = new ethers.providers.Web3Provider(instance)

    // set svelte state
    await defaultEvmStores.setProvider(provider)
    
    const { chainId } = await provider.getNetwork()

    // attach Shapes contract
    const { address, abi } = await getContractInfo(chainId)
    console.log(`SHAPES contract: ${address}`)
    await defaultEvmStores.attachContract('shapes', address, abi)

    // attach Slashes contract
    const { address: slashesAddress, abi: slashesAbi } = await getContractInfo(chainId, 'SlashesNFT')
    console.log(`SLASHES contract: ${slashesAddress}`)
    await defaultEvmStores.attachContract('slashes', slashesAddress, slashesAbi)
  } catch (e) {
    if (e.code == '4001') {
      message.set(e.message)
    } else {
      message.set("Couldn't get the wallet connection")
    }
    message.set("Couldn't get a wallet connection")
    console.log(e)
  }
}
